const context = require("../../context/Java110Context.js");
const constant = context.constant;
import noDataPage from '@/components/no-data-page/no-data-page.vue'
export default {
	data() {
		return {
			owners: [],
			page: 1,
			totalPage: 0,
			loading: false,
			communityId: '',
			ownerId: ''
		};
	},
	components: {
		noDataPage
	},
	onLoad: function() {

	},
	onShow: function() {
		let that = this;
		context.getOwner(function(_owner) {
			console.log(_owner.communityId, 99999999);
			that.communityId = _owner.communityId;
			that.ownerId = _owner.memberId;
			that.getTable(1);
		});
	},
	onPullDownRefresh: function() {
		// 上拉刷新
		if (!this.loading) {
			this.getTable(1, true).then(() => {
				// 处理完成后，终止下拉刷新
				wx.stopPullDownRefresh();
			});
		}
	},
	onReachBottom: function() {
		console.log(1, !this.loading, this.page < this.totalPage); // 下拉触底，先判断是否有请求正在进行中
		// 以及检查当前请求页数是不是小于数据总页数，如符合条件，则发送请求

		if (!this.loading && this.page < this.totalPage) {
			this.getTable(this.page + 1);
		}
	},
	methods: {
		getTable: function(page, override) {
			let that = this;
			this.loading = true;
			let _paramIn = {
				"communityId": that.communityId,
				"ownerId": that.ownerId
			};
			context.request({
				url: constant.url.queryOwnerMembers,
				header: context.getHeaders(),
				method: "GET",
				data: _paramIn,
				success: function(res) {
					if (res.statusCode == 200) {
						let _owners = res.data.owners;
						that.owners = _owners;
						that.loading = false;
					}
				},
				fail: function(e) {
					wx.showToast({
						title: "服务器异常了",
						icon: 'none',
						duration: 2000
					})
				}
			})
		},
		goAdd: function(e) {
			uni.navigateTo({
				url: "/pages/family/family"
			})
		}
	}
};
